import Question1 from '../Question1';
import Adapter from 'enzyme-adapter-react-16';
import {shallow} from 'enzyme';
import * as enzyme from 'enzyme';
import {useNavigation} from '@react-navigation/native';
import {NativeStackNavigationProp} from '@react-navigation/native-stack/lib/typescript/src/types';
import React, {useContext} from 'react';
import {render, waitFor} from '@testing-library/react-native';
import {Text, Button, Pressable} from 'react-native';
import {QuizContext} from './../QuizContext';
enzyme.configure({adapter: new Adapter()});
const navigation = useNavigation<NativeStackNavigationProp<any>>();

jest.mock('@react-navigation/native', () => {
  return {
    useNavigation: jest.fn().mockReturnValue({
      navigate: jest.fn(),
    }),
  };
});

describe('Question 1 component', () => {
  const mockQuizContext = {
    score: 0,
    setScore: jest.fn(),
    name: 'chandu',
    setName: jest.fn(),
    email: 'chandra@123.com',
    setMail: jest.fn(),
    phone: '123',
    setPhone: jest.fn(),
    selectedValue: 'ReactJS',
    setSelected: jest.fn(),
    trackScore: true,
    setTracker: jest.fn(),
  };
  let wrapper: any;
  beforeEach(() => {
    wrapper = shallow(
      <QuizContext.Provider value={mockQuizContext}>
        <Question1 />
      </QuizContext.Provider>,
    );
  });
  afterEach(() => {
    jest.restoreAllMocks();
  });
  const questionText = 'What is the extension used for TypeScript in ReactJS?';

  it('the component renders properly', () => {
    expect(wrapper.find('[testID="questionID"]').text()).toEqual(
      'What is the extension used for TypeScript in ReactJS?',
    );
  });
});
